export const updateStudent = async ( student, id) => {
  console.log(id, typeof id);
  const options = {
    method: "PATCH",
    body: JSON.stringify(student),
    headers: {
      "Content-Type": "application/json; charset=UTF-8",
    },
  };

  try {
    return await fetch(
      `http://localhost:3000/students/${id}`,
      options
    ).then((response) => response.json());
  } catch (error) {
    console.log(error);
  }
};
